import React from "react";
import ReactDOM from "react-dom/client";
import ChatWidget from "./components/ChatWidget";

// üü¢ Glob√°ln√≠ objekt pro vlo≈æen√≠ do str√°nky (nap≈ô. p≈ôes <script>)
window.ChatbotWidget = {
  init: (options = {}) => {
    // üö´ Zabra≈à duplicitn√≠mu vlo≈æen√≠
    if (document.getElementById("chatbot-widget-container")) return;

    // ‚úÖ Vytvo≈ô√≠ kontejner pro widget
    const el = document.createElement("div");
    el.id = "chatbot-widget-container";
    document.body.appendChild(el);

    // üîÅ React render
    const root = ReactDOM.createRoot(el);
    root.render(<ChatWidget {...options} />);
  }
};
